import WebGLConstants from '../Core/WebGLConstants.js';
import EnvironmentVariables from '../Extension/Source/EnvironmentVariables.js';

/**
 * @private
 */
var PixelDatatype = {
    _UNSIGNED_BYTE : WebGLConstants.UNSIGNED_BYTE,
    _UNSIGNED_SHORT : WebGLConstants.UNSIGNED_SHORT,
    _UNSIGNED_INT : WebGLConstants.UNSIGNED_INT,
    _FLOAT : WebGLConstants.FLOAT,
    _HALF_FLOAT : WebGLConstants.HALF_FLOAT_OES,
    _UNSIGNED_INT_24_8 : WebGLConstants.UNSIGNED_INT_24_8,
    _UNSIGNED_SHORT_4_4_4_4 : WebGLConstants.UNSIGNED_SHORT_4_4_4_4,
    _UNSIGNED_SHORT_5_5_5_1 : WebGLConstants.UNSIGNED_SHORT_5_5_5_1,
    _UNSIGNED_SHORT_5_6_5 : WebGLConstants.UNSIGNED_SHORT_5_6_5
};

Object.defineProperties(PixelDatatype, {
    UNSIGNED_BYTE : {
        get : function() {
            return PixelDatatype._UNSIGNED_BYTE;
        }
    },
    UNSIGNED_SHORT : {
        get : function() {
            return PixelDatatype._UNSIGNED_SHORT;
        }
    },
    UNSIGNED_INT : {
        get : function() {
            return PixelDatatype._UNSIGNED_INT;
        }
    },
    FLOAT : {
        get : function() {
            return PixelDatatype._FLOAT;
        }
    },
    HALF_FLOAT : {
        get : function() {
            return EnvironmentVariables.isWebgl2 ? WebGLConstants.HALF_FLOAT : PixelDatatype._HALF_FLOAT;
        }
    },
    UNSIGNED_INT_24_8 : {
        get : function() {
            return PixelDatatype._UNSIGNED_INT_24_8;
        }
    },
    UNSIGNED_SHORT_4_4_4_4 : {
        get : function() {
            return PixelDatatype._UNSIGNED_SHORT_4_4_4_4;
        }
    },
    UNSIGNED_SHORT_5_5_5_1 : {
        get : function() {
            return PixelDatatype._UNSIGNED_SHORT_5_5_5_1;
        }
    },
    UNSIGNED_SHORT_5_6_5 : {
        get : function() {
            return PixelDatatype._UNSIGNED_SHORT_5_6_5;
        }
    }
});

PixelDatatype.isPacked = function(pixelDatatype) {
    return pixelDatatype === PixelDatatype.UNSIGNED_INT_24_8 ||
           pixelDatatype === PixelDatatype.UNSIGNED_SHORT_4_4_4_4 ||
           pixelDatatype === PixelDatatype.UNSIGNED_SHORT_5_5_5_1 ||
           pixelDatatype === PixelDatatype.UNSIGNED_SHORT_5_6_5;
};

PixelDatatype.sizeInBytes = function(pixelDatatype) {
    switch (pixelDatatype) {
        case PixelDatatype.UNSIGNED_BYTE:
            return 1;
        case PixelDatatype.UNSIGNED_SHORT:
        case PixelDatatype.UNSIGNED_SHORT_4_4_4_4:
        case PixelDatatype.UNSIGNED_SHORT_5_5_5_1:
        case PixelDatatype.UNSIGNED_SHORT_5_6_5:
        case PixelDatatype.HALF_FLOAT:
            return 2;
        case PixelDatatype.UNSIGNED_INT:
        case PixelDatatype.FLOAT:
        case PixelDatatype.UNSIGNED_INT_24_8:
            return 4;
    }
};

PixelDatatype.validate = function(pixelDatatype) {
    return ((pixelDatatype === PixelDatatype.UNSIGNED_BYTE) ||
            (pixelDatatype === PixelDatatype.UNSIGNED_SHORT) ||
            (pixelDatatype === PixelDatatype.UNSIGNED_INT) ||
            (pixelDatatype === PixelDatatype.FLOAT) ||
            (pixelDatatype === PixelDatatype.HALF_FLOAT) ||
            (pixelDatatype === PixelDatatype.UNSIGNED_INT_24_8) ||
            (pixelDatatype === PixelDatatype.UNSIGNED_SHORT_4_4_4_4) ||
            (pixelDatatype === PixelDatatype.UNSIGNED_SHORT_5_5_5_1) ||
            (pixelDatatype === PixelDatatype.UNSIGNED_SHORT_5_6_5));
};
export default Object.freeze(PixelDatatype);
