import Cartesian3 from '../../Core/Cartesian3.js';
import defined from '../../Core/defined.js';
import DeveloperError from '../../Core/DeveloperError.js';
import Ellipsoid from '../../Core/Ellipsoid.js';

var EnvironmentVariables = {
    //TAA
    _enableTAA: false,
    _jitterOffset: [0, 0],
    _currentFrameTaaEnabled: false,
    _historicalInvalid: false,

    //performance mode
    _ultimatePerformance: true,

    //light
    _ambientLight: new Cartesian3(0.2, 0.2, 0.2),
    _lightScaleFactor: 1.0,

    //固定帧率
    _gradualRenderMode: false,
    //背景渐变
    _vignetteMode: false,

    _isWebgl2: false,
    _multipleSamples: 4,

    _isBIMMode: true,
    _ellipsoidType: Ellipsoid.WGS84,
    _isFlashlight: false,
    _enableSMAA: false
};

Object.defineProperties(EnvironmentVariables, {
    enableTAA: {
        get: function () {
            return EnvironmentVariables._enableTAA;
        },
        set: function (value) {
            EnvironmentVariables._enableTAA = value;
            if (value) {
                //重新开启多重采样的时候，需要重置
                EnvironmentVariables.historicalInvalid = value;
            }
        }
    },

    jitterOffset: {
        get: function () {
            return EnvironmentVariables._jitterOffset;
        },
        set: function (value) {
            EnvironmentVariables._jitterOffset = value;
        }
    },

    historicalInvalid: {
        get: function () {
            return EnvironmentVariables._historicalInvalid;
        },
        set: function (value) {
            EnvironmentVariables._historicalInvalid = value;
        }
    },

    currentFrameTaaEnabled: {
        get: function () {
            return EnvironmentVariables._currentFrameTaaEnabled;
        },
        set: function (value) {
            EnvironmentVariables._currentFrameTaaEnabled = value;
        }
    },

    ultimatePerformance: {
        get: function () {
            return EnvironmentVariables._ultimatePerformance;
        },
        set: function (value) {
            EnvironmentVariables._ultimatePerformance = value;
        }
    },

    ambientLight: {
        get: function () {
            return EnvironmentVariables._ambientLight;
        },
        set: function (value) {
            if (!defined(value)) {
                throw new DeveloperError('ambientLight must be defined');
            } else if (!(value instanceof Cartesian3)) {
                throw new DeveloperError('ambientLight must be an instance of Cartesian3');
            }
            EnvironmentVariables._ambientLight = value;
        }
    },

    lightScaleFactor: {
        get: function () {
            return EnvironmentVariables._lightScaleFactor;
        },
        set: function (value) {
            if (!defined(value || value < 0.0)) {
                throw new DeveloperError('lightScaleFactor must be greater than or equal to 0.0.');
            }
            EnvironmentVariables._lightScaleFactor = value;
        }
    },

    gradualRenderMode: {
        get: function () {
            return EnvironmentVariables._gradualRenderMode;
        },
        set: function (value) {
            EnvironmentVariables._gradualRenderMode = value;
        }
    },

    vignetteMode: {
        get: function () {
            return EnvironmentVariables._vignetteMode;
        },
        set: function (value) {
            EnvironmentVariables._vignetteMode = value;
        }
    },

    isWebgl2: {
        get: function () {
            return EnvironmentVariables._isWebgl2;
        },
        set: function (value) {
            EnvironmentVariables._isWebgl2 = value;
        }
    },

    multipleSamples: {
        get: function () {
            return EnvironmentVariables._multipleSamples;
        },
        set: function (value) {
            EnvironmentVariables._multipleSamples = value;
        }
    },

    isBIMMode: {
        get: function () {
            return EnvironmentVariables._isBIMMode;
        },
        set: function (value) {
            EnvironmentVariables._isBIMMode = value;
        }
    },

    ellipsoidType: {
        get: function () {
            return EnvironmentVariables._ellipsoidType;
        },
        set: function (value) {
            EnvironmentVariables._ellipsoidType = value;
        }
    },
    isFlashlight: {
        get: function () {
            return EnvironmentVariables._isFlashlight;
        },
        set: function (value) {
            EnvironmentVariables._isFlashlight = value;
        }
    },
    enableSMAA: {
        get: function () {
            return EnvironmentVariables._enableSMAA;
        },
        set: function (value) {
            EnvironmentVariables._enableSMAA = value;
        }
    }
});
export default EnvironmentVariables;
